    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[]
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
        methods: {  
            ...mapMutations(['logout','setUserInfo']),  
            async loadData() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }else{
					_this.$Request.post('member_account').then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.setUserInfo(res.datas);							
							_this.memberInfo = res.datas;
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'../login',
							});
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toLogout: function(url) {
				this.logout();
				uni.navigateTo({
					url:'../login',
				});
			},
			toBack(url){
				uni.switchTab({
					url:'/pages/member/home', 
					success (res) {
							const currentPages = getCurrentPages()  
							console.log(currentPages[0]);  
							currentPages[0].onLoad({  
								...currentPages[0].options  
							})
					}
				})
			}
			 
        }  
    }  
